<template>
	<div class="login-container"  style="background: url('http://maizheyou.oss-cn-qingdao.aliyuncs.com/mzy16705d383661.jpg') 50% 50%">

  <el-form ref="AccountFrom"  :model="account" :rules="rules" v-bind:style="{width:Width}" label-position="left" label-width="0px"
           class="demo-ruleForm ">
    <h3 class="title" style="text-align: center;">商铺后台系统</h3>
    <el-form-item prop="username">
            <!--<span class="svg-container svg-container_login">
          <svg-icon icon-class="user" />
        </span>-->
      <el-input type="text" v-model="account.username" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <!--vue使用element-ui的el-input监听不了回车事件，原因应该是element-ui自身封装了一层input标签之后，把原来的事件隐藏了-->
    <el-form-item prop="pwd">
      <el-input type="password"  @keyup.enter.native="handleLogin" v-model="account.pwd" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <!--<el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>-->
    <el-form-item style="width:100%;margin-top:1rem;border:0">
      <el-button type="primary"  style="width:100%;border:0;background:#0077ed"  @click.native.prevent="handleLogin" :loading="logining" >登录</el-button>
    </el-form-item>
  </el-form>

	</div>
</template>


<script>

  import qs from 'qs'

  // import {requestLogin} from '../api/api';
  //import NProgress from 'nprogress'
  export default {
  	name:'login',
    data() {
      return {
        Width:"",

        logining: false,
        account: {
          username: '',
          pwd: ''
        },
        rules: {
          username: [
            {required: true, message: '请输入账号', trigger: 'blur'},
            //{ validator: validaePass }
          ],
          pwd: [
            {required: true, message: '请输入密码', trigger: 'blur'},
            //{ validator: validaePass2 }
          ]
        },
        checked: true
      };
    },


    mounted() {
			this.getCookies();
      if (this._isMobile()) {
        console
        this.Width = "70%"
      } else {

        this.Width = "20%"
      }
      ;
    },
    methods: {

			//设置cookies
			setCookies: function(account,pwd){
				var curdate = new Date();
				curdate.setTime(curdate.getTime()+24*60*60*1000);
				window.document.cookie = "username="+account+";path=/;expires="+curdate.toGMTString();
				window.document.cookie = "userpwd="+pwd+";path=/;expires="+curdate.toGMTString();

			},
			getCookies:function(){
				console.log(window.document.cookie)
				var arr = window.document.cookie.split(";")
				for(var i =0;i<arr.length;i++){
					var arr2 = arr[i].split("=");
					if(arr2[0] =="username"){
						this.account.username = arr2[1]
					}else{
						this.account.pwd = arr2[1]
					}
				}
			},
			clearCookies:function(){
				this.setCookies('','',-1)
			},
        _isMobile() {
          let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
          return flag;
        },

      handleLogin() {
				this.setCookies(this.account.username,this.account.pwd)
        var loginParams = { mobile: this.account.username, passwd: this.account.pwd };
        var param=qs.stringify(loginParams);
        this.$axios.post(this.$url.login,param).then(function (res) {
          console.log(res);
          if(res.data.code==200) {

            sessionStorage.setItem("access-token", res.data.data.token)
            sessionStorage.setItem("mobile",this.account.username)
            this.$router.push({path: '/home'});
            console.log("登陆")

          }else if(res.data.code==400){
            alert("密码错误")
          }
        }.bind(this)).catch(function (err) {
          console.log(err);

        })
        //mock 模拟登陆
        // this.$refs.AccountFrom.validate((valid) => {
        //   if (valid) {
        //
        //     this.logining = true;
        //     //NProgress.start();
        //     var loginParams = { username: this.account.username, password: this.account.pwd };
        //   //    let loginParams = new URLSearchParams();
        //   //    loginParams.append("username",this.account.username);
        //    //   loginParams.append("password",this.account.pwd);
        //     requestLogin(loginParams).then(data => {
        //      debugger;
        //       this.logining = false;
        //
        //       let { msg, code, token } = data;
        //       if(code == '200'){
        //         //登录成功，把用户信息保存在sessionStorage中
        //         sessionStorage.setItem('access-token', token);
        //         //跳转到后台主界面
        //         this.$router.push({ path: '/home' });
        //       }else{
        //         this.$message({
        //           message: msg,
        //           type: 'error'
        //         });
        //
        //       }
        //
        //     });
        //
        //   } else {
        //     console.log('error submit!!');
        //     return false;
        //   }
        // });
      }
    }
  }

</script>

<style rel="stylesheet/scss" lang="scss">
  $bg:#2d3a4b;
  $dark_gray:#889aa4;
  $light_gray:#eee;

body{margin:0;font-weight: 200!important;font-weight: 100!important;
}

  .login-container {
    position: fixed;
    height: 100%;
    width:100%;
background-size:auto;
    input:-webkit-autofill {
      -webkit-box-shadow: 0 0 0px 1000px #293444 inset !important;
      -webkit-text-fill-color: #fff !important;
    }
    .demo-ruleForm{
    	width:20%;
    	margin:0 auto;
    	margin-top:120px;
    }
    input {
      background: transparent;
      border: 0px;
      -webkit-appearance: none;
      border-radius: 0px;
      padding: 12px 5px 12px 15px;
      color: white;
      height: 40px;
    }
    .el-input {
      display: inline-block;
      height: 40px;

    }
    .tips {
      font-size: 14px;
      color: #fff;
      margin-bottom: 10px;
    }
    .svg-container {
      padding: 6px 5px 6px 15px;
      color: $dark_gray;
      vertical-align: middle;
      width: 30px;
      display: inline-block;
      &_login {
        font-size: 20px;
      }
    }
    .title {
      font-size: 26px;
      font-weight: 300;
      color: white;
      margin: 0px auto 40px auto;
      text-align: center;

    }
    .login-form {
      position: absolute;
      left: 0;
      right: 0;
      width: 400px;
      padding: 35px 35px 15px 35px;
      margin: 120px auto;
    }
    .el-form-item {
      border: 1px solid rgba(255, 255, 255, 0.5);

      border-radius: 5px;
      color: #454545;
    }
    .show-pwd {
      position: absolute;
      right: 10px;
      top: 7px;
      font-size: 16px;
      color: $dark_gray;
      cursor: pointer;
      user-select:none;
    }
    .thirdparty-button{
      position: absolute;
      right: 35px;
      bottom: 28px;
    }
  }
</style>



